import React from 'react';
import Image from 'next/image';
import { Card, CardContent } from '@/components/ui/card';


const page = () => {
  return (
    <div className="container mx-auto p-4">
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
        {movies.map((movie) => (
          <Card 
            key={movie.id} 
            className="group relative overflow-hidden transition-all duration-300 hover:scale-105"
          >
            <div className="aspect-[2/3] relative">
              <Image
                src={movie.posterUrl}
                alt={movie.title}
                className="object-cover rounded-t-lg"
                fill
                sizes="(max-width: 768px) 50vw, (max-width: 1200px) 33vw, 20vw"
                priority={false}
              />
              <div className="absolute inset-0 bg-black/60 opacity-0 group-hover:opacity-100 transition-opacity duration-300 flex flex-col justify-end p-4">
                <h3 className="text-white font-bold text-lg mb-1">{movie.title}</h3>
                <p className="text-white/80 text-sm">{movie.year}</p>
                {movie.rating && (
                  <div className="absolute top-2 right-2 bg-yellow-400 text-black px-2 py-1 rounded-full text-sm font-bold">
                    {movie.rating}
                  </div>
                )}
              </div>
            </div>
          </Card>
        ))}
      </div>
    </div>
  )
}

export default page